<template>
  <component
    class="button"
    :is="buttonTag"
    :href="href"
    :type="type"
  >
    <slot></slot>
  </component>
</template>

<script>
export default {
  name: "BaseButton",
  data(){
    return {
      active: false,
    }
  },
  props: {
    href: {
      required: false,
      type: String,
      default: null
    },
    type: {
      type: String,
      default: "button" //button, submit
    },
    label: {
      type: String,
      defalut: "",
    },
  },

  computed: {
    buttonTag() {
      if (this.href) {
        return "a";
      } else {
        return "button";
      }
    }
  },
  methods: {
    handleClick(evt) {
      this.$emit("click", evt);
    },
  }
};
</script>
<style lang="scss">



/*button - reset*/
button:focus,
button[type="button"]:focus,
button[type="reset"]:focus,
button[type="submit"]:focus{outline:none;box-shadow: none;}

button:focus-visible,
button:not(.disabled):focus-visible{outline:2px solid #B0EBEC;}

.button{
  display:inline-flex;align-items:center;justify-content:center;border-radius:$btn-radius-default;cursor: pointer;flex-shrink: 0;

  /*type*/
  &.primary{color:#fff;border:1px solid $primary-bg-color;background-color:$primary-bg-color;}
  &.secondary{color:$primary-text-color;border:1px solid $primary-border-color;background-color:#fff;}
  &.outline{color:#4b4b4b;border:1px solid #EEEEEE;background-color:#fff;}
  &.disabled{color:#fff;border:1px solid #e1e1e1;background-color:#e1e1e1;cursor:default}
  &.ghost{background-color:transparent;border:1px solid transparent;}

  /*state*/
  &.primary:hover{color:#fff;border:1px solid $primary-border-hover-color;background-color:$primary-bg-hover-color;}
  &.primary.disabled{color:#fff;border:1px solid #e1e1e1;background-color:#e1e1e1;cursor:default}

  &.outline:hover{background-color:#F5F5F5;}
  &.outline.disabled{color:#CACACA;border:1px solid #EEEEEE;background-color:#fff;}

  &.ghost:hover{background-color:transparent;border:1px solid transparent;}
  &.ghost.disabled{color:#CACACA;}

  /*layout*/
  .label{display:inline-flex;}
  i + .label{margin-left:4px;}
  .label + i{margin-left:4px;}

  /*size*/
  &.sm{height:24px;line-height:24px;padding:0 6px;font-size:12px;}
  &.md{height:32px;line-height:32px;padding:0 8px;}
  &.lg{height:40px;line-height:40px;padding:0 24px;}

  &.icoOnly{
    /*size*/
    &.sm{height:24px;line-height:24px;width:24px;padding:0;}
    &.md{height:32px;line-height:32px;width:32px;padding:0;}
    &.lg{height:40px;line-height:40px;width:40px;padding:0;}
  }

  &.circle{
    border-radius: 50%;
  }
}

</style>