};

export default {
  props: {
    name: String,
    color:{
        type: String,
        required: true,
    },
    width: {
        type: Number,
        default: 20
    },
    height: {
        type: Number,
        default: 20
    },
    label:{
      type : String,
      required: true,
    },
  },
  computed: {
    path() {
      return icons[this.name];
    },
  }
};
