<style lang="scss">
  $icon-color-gray: #CACACA;
  $icon-color-yellow: #FFAD0D;
  $icon-color-blue:#3B82F6;

  .ico{
    display:inline-flex;width:20px;height:20px;color:#8E8E8E;

    //사이즈
    &.w12{width:12px;height:12px;}
    &.w14{width:14px;height:14px;}
    &.w16{width:16px;height:16px;}
    &.w20{width:20px;height:20px;}
    &.w24{width:24px;height:24px;}
    &.w32{width:32px;height:32px;}
    &.w40{width:40px;height:40px;}
  }
  
  .col_primary{color:$color-primary;}
  .col_gray{color:$icon-color-gray;}
  .col_white{color:#fff;}
  .col_yellow{color:$icon-color-yellow;}
</style>