import { createWebHistory, createRouter } from 'vue-router'

const routes = [
    {
        path: '/',
        name: 'MainView',
        component: () => import('../views/template/mainView.vue')
    },
    // {
    //     path: 'views/page',
    //     name: 'mainView',
    //     component: () => import(/* webpackChunkName: "lottomainview" */ '@/views/page/mainView.vue')
    // },
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router